import axios, {AxiosInstance} from 'axios';
import config_prod from './config_prod';
import config_dev from './config_dev';

let connection = null ;

const URL_SIGNUP = '/api/signup';
const URL_LOGIN = '/api/login';

const URL_CURRENCIES = '/api/currencies';
const URL_WALLETS = '/api/wallets';
const URL_ITEMS_EXPENDITURE = '/api/itemsexpense';
const URL_ITEMS_INCOME = '/api/itemsexpense';

const routesMap = new Map();

routesMap.set('currencies', URL_CURRENCIES);
routesMap.set('wallets', URL_WALLETS);
routesMap.set('itemsexpense', URL_ITEMS_EXPENDITURE);
routesMap.set('URL_ITEMS_INCOME', URL_ITEMS_INCOME);

const isProduction = process.env.NODE_ENV === 'production';

class Connection {

    constructor() {
        this.http = axios.create({
            timeout: 5000,
            headers: {
                'Content-Type': 'application/json',
                Accept: 'application/json',
            },
            proxy: false,
            data: {},
        });

        this.host = isProduction ? config_prod.host : config_dev.host;
    }

    addAuthHeader() {
        let token = sessionStorage.getItem('jwt');

        this.http.defaults.headers.common['Authorization'] = `Bearer ${token}`;
    }

    async login(login, password) {
        const res = await this.http.post(URL_LOGIN, { login: login, password: password });
        return res.data;
    }

    async signup(user) {
        try {
            const res = await this.http.post(URL_SIGNUP, user);
            return res.data;
        } catch (err) {
            throw err.response.data;
        }
    }

    async index(model, queryParams) {

        this.addAuthHeader();

        let url = routesMap.get(model);

        let res = await this.http.get(url, {
            params: queryParams
        });

        return res.data;
    }

    async show(id, model) {}

    async store(model, data) {
        this.addAuthHeader();
        let url = routesMap.get(model) + '?XDEBUG_SESSION_START=11870';

        try{
            let res = await this.http.post(url, data);
            return res.data;
        } catch (err) {
            throw err.response.data;
        }

    }

    async update(model, id, data) {
        this.addAuthHeader();
        let url = `${routesMap.get(model)}/${id}`;

        try{
            let res = await this.http.put(url, data);
            return res.data;
        } catch (err) {
            throw err;
        }
    }
}

export default function getConn() {
    return connection ? connection : new Connection();
}



