<template>
    <v-container>
        <v-layout wrap>
            <v-flex xs12 sm12 md12 lg12>
                <v-dialog v-if="dialog" v-model="dialog" max-width="500px" persistent>
                  <currency-element
                          v-bind:item = "this.editedItem"
                          @cancel ="dialog = false"
                          @stored = "dialog = false; update()"
                  ></currency-element>
                </v-dialog>

                <v-data-table
                    :headers="headers"
                    :items="items"
                    :loading="updating"
                    hide-actions
                    class="elevation-1"
                >
                    <template slot="items" slot-scope="props">
                        <td class="d-none">{{ props.item.id }}</td>
                        <td @click="edit(props.item)">{{ props.item.code }}</td>
                        <td @click="edit(props.item)">{{ props.item.name }}</td>
                        <td @click="edit(props.item)" class="hidden-md-and-down">
                            {{ props.item.short_name }}
                        </td>
                    </template>
                    <template slot="no-data"> </template>
                </v-data-table>
            </v-flex>

            <v-fab-transition>
                <v-btn fab fixed bottom right dark @click="add()" color="primary">
                    <v-icon>add</v-icon>
                </v-btn>
            </v-fab-transition>

            <v-snackbar
                :timeout="msgSettings.timeout"
                :color="msgSettings.color"
                :multi-line="msgSettings.mode === 'multi-line'"
                :vertical="msgSettings.mode === 'vertical'"
                v-model="msgSettings.show"
            >
                {{ msgSettings.msg }}
                <v-btn dark flat @click.native="msgSettings.show = false">Close</v-btn>
            </v-snackbar>
        </v-layout>
    </v-container>
</template>

<script>
import ApiClass from '../../api/api_laravel';
import currencyElement from './CurrencyElement';
const api = ApiClass();

import { mapGetters, mapActions } from 'vuex';

export default {
    data: () => ({
        dialog: false,
        id: null,
        editItem: null,
        offsetTop: 0,
        offset: 0,
        updating: false,
        headers: [
            { text: 'id', value: 'id', class: 'd-none' },
            { text: 'Code', value: 'code' },
            { text: 'Name', value: 'name' },
            {
                text: 'Short name',
                value: 'short_name',
                class: 'hidden-md-and-down',
                sortable: false,
            },
        ],

        editedIndex: -1,

        msgSettings: {
            show: false,
            color: 'light-green darken-3',
            mode: 'vertical',
            timeout: 6000,
            msg: '',
        },
    }),
    components: {
        'currency-element': currencyElement
    },
    computed: mapGetters({
        items: 'allCurrencies',
    }),

    watch: {
        dialog(val) {
            val || this.close();
        },
    },

    beforeMount: function() {

        this.$store.dispatch('getAllCurrencies');

    },
    methods: {
        getUpMenu() {
            let menu = [];

            const action1 = {
                title: 'Update',
                icon: 'cached',
                action: () => {
                    this.update();
                },
            };

            const action2 = {
                title: 'Load rates',
                icon: 'move_to_inbox',
                action: () => {
                    this.$router.push({ path: 'loadrates' });
                },
            };
            const action3 = {
                title: 'Add from classifitator',
                icon: 'format_list_bulleted',
                action: () => {
                    this.$router.push({ path: 'loadcurrency' });
                },
            };

            menu.push(action1);
            menu.push(action2);
            menu.push(action3);

            return menu;
        },

        update() {
            if (this.updating == false) {
                this.items = [];
                this.updating = true;
                this.$store.dispatch('getAllCurrencies');
                this.updating = false;
            }
        },

        sendData(item) {
            let isUpdate = false;

            if (item.id !== null) {
                isUpdate = true;
            }
            const param = {
                model: 'currency',
                isUpdate: isUpdate,
                data: item,
            };

            Api.save(param).then(success => {
                if (success === true) {
                    this.showMsg(true);
                    this.update();
                    this.close();
                    return true;
                } else {
                    this.showMsg(false);
                    return false;
                }
            });
        },
        edit(item) {
            console.dir(item);

            this.id = item.id;
            this.editedIndex = this.items.indexOf(item);
            this.editedItem = Object.assign({}, item);

            this.dialog = true;
        },
        add() {
            this.id = null;
            this.dialog = true;
        },
        deleteItem(item) {
            const index = this.items.indexOf(item);
            confirm('Are you sure you want to delete this item?') && this.items.splice(index, 1);
        },

        close() {
            this.dialog = false;
            this.formTitle = 'New';
            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            }, 300);
        },

        save() {
            let ok = this.sendData(this.editedItem);
            if (ok) {
                this.update();
                this.close();
            }
        },

        showMsg(success) {
            this.msgSettings.color = success ? 'light-green darken-3' : 'orange darken-4';
            this.msgSettings.msg = success ? 'Saved/Updated successfully!' : 'Error';
            this.msgSettings.show = true;
        },
    },
};
</script>

<style scoped>
.td_hidden {
    display: none;
}

.progress {
    text-align: center;
    margin: 1rem;
}

#create .speed-dial {
    position: absolute;
}

#create .btn--floating {
    position: relative;
}
</style>



